@import '@styles/vars.scss';
@import '@styles-mixins/media.scss';
@import '@styles-mixins/hoverEffects.scss';


.card-review {
  &__inner {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 400px;
    box-sizing: border-box;
    padding: 56px 24px 16px 32px;
    border-radius: 20px;
    background: #fff;

    @include media(from_sm) {
      height: 647px;
      padding: 96px 40px 24px 56px;
    }

    @include media(from_m) {
      height: 300px;
      padding: 32px 16px 16px 32px;
    }

    @include media(from_l) {
      height: 393px;
      padding: 48px 16px 16px 32px;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      z-index: 1;
      pointer-events: none;

      width: 56px;
      height: 50px;

      background-repeat: no-repeat;
      background-size: 100% auto;
      background-position: center;
      background-image: url("data:image/svg+xml,%3Csvg width='56' height='50' viewBox='0 0 56 50' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 0H56V50H20C8.95431 50 0 41.0457 0 30V0Z' fill='%23D5F0FF'/%3E%3Cpath d='M0 0H56V50L0 0Z' fill='%23F1FAFF'/%3E%3C/svg%3E%0A");

      
      @include media(from_sm) {
        top: -4px;
        width: 85px;
        height: 84px;
      }

      @include media(from_m) {
        width: 35px;
        height: 37px;
      }

      @include media(from_l) {
        width: 47px;
        height: 49px;
      }
    }
  }

  &__source {
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    font-size: 12px;
    line-height: 150%;
    text-decoration: none;
    color: $blue-light;

    @include defaultHover();

    @include media(from_sm) {
      font-size: 20px;
    }

    @include media(from_m) {
      font-size: 10px;
    }

    @include media(from_l) {
      font-size: 12px;
    }
  }

  &__content {
    position: relative;
    max-height: 264px;
    margin-bottom: 16px;
    overflow: hidden;

    font-size: 16px;
    line-height: 150%;

    @include media(from_sm) {
      max-height: 424px;
      margin-bottom: 56px;

      font-size: 26px;
    }

    @include media(from_m) {
      max-height: 200px;
      margin-bottom: 8px;

      font-size: 10px;
    }

    @include media(from_l) {
      max-height: 264px;
      margin-bottom: 10px;

      font-size: 12px;
    }
  }

  &__content p {
    margin: 0;
  }

  &__content p + p {
    margin-top: 8px;  
  }

  &__button-more {
    position: absolute;
    bottom: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;

    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    height: 20px;
    box-sizing: border-box;
    background-color: #fff;
    box-shadow: 0px -16px 40px rgba(212, 240, 255, 0.48);

    @include defaultHover();
  }

  &__button-more_shown {
    opacity: 1;
    pointer-events: all;
  }

  &__button-more-icon {
    padding: 4px 8px;
    background: #D4F0FF;
    border-radius: 100px;
  }

  &__button-more-dots {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: -4px;
  }

  &__button-more-dot {
    display: inline-block;
    width: 4px;
    height: 4px;
    margin-left: 4px;
    border-radius: 50%;
    background: linear-gradient(0deg, #3AB3FE, #3AB3FE), #3AB3FE;
  }

  &__author {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: auto;
  }

  &__author-name {
    max-width: 168px;
    text-align: right;
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 120%;
    color: #4F4F4F;

    @include media(from_sm) {
      max-width: 232px;
      font-size: 20px;
    }

    @include media(from_m) {
      max-width: 120px;
      font-size: 10px;
    }

    @include media(from_l) {
      max-width: 148px;
      font-size: 12px;
    }
  }

  &__author-pic-wrapper {
    flex-shrink: 0;
    width: 50px;
    height: 50px;
    margin-left: 16px;
    overflow: hidden;
    border-radius: 50%;

    background: url("data:image/svg+xml,%3Csvg width='13' height='15' viewBox='0 0 13 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.1668 14.893H2.93185C2.21229 14.893 1.52219 14.6071 1.01338 14.0983C0.504578 13.5895 0.21875 12.8995 0.21875 12.1799C0.21875 11.4603 0.504578 10.7702 1.01338 10.2614C1.52219 9.75264 2.21229 9.4668 2.93185 9.4668H10.1668C10.8863 9.4668 11.5764 9.75264 12.0852 10.2614C12.5941 10.7702 12.8799 11.4603 12.8799 12.1799C12.8799 12.8995 12.5941 13.5895 12.0852 14.0983C11.5764 14.6071 10.8863 14.893 10.1668 14.893Z' fill='%236DCDFC'/%3E%3Cpath d='M6.77608 6.77188C8.56168 6.77188 10.0092 5.32437 10.0092 3.53878C10.0092 1.75318 8.56168 0.305664 6.77608 0.305664C4.99048 0.305664 3.54297 1.75318 3.54297 3.53878C3.54297 5.32437 4.99048 6.77188 6.77608 6.77188Z' fill='url(%23paint0_linear_437_5108)'/%3E%3Cdefs%3E%3ClinearGradient id='paint0_linear_437_5108' x1='3.53845' y1='3.53877' x2='10.0092' y2='3.53877' gradientUnits='userSpaceOnUse'%3E%3Cstop offset='0.26' stop-color='white'/%3E%3Cstop offset='1' stop-color='%238CD0FD'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E%0A") no-repeat center/16px auto, linear-gradient(90deg, #3AB3FE 26%, #3AB3FE 99.99%);

    @include media(from_sm) {
      width: 80px;
      height: 80px;
      margin-left: 24px;
    }

    @include media(from_m) {
      width: 40px;
      height: 40px;
      margin-left: 16px;
    }

    @include media(from_l) {
      width: 48px;
      height: 48px;
      margin-left: 16px;
    }
  }
}

.card-review_type_video {
  .card-review {
    &__inner {
      &::after {
        content: none;
      }
    }

    &__video-player {
      position: absolute;
      top: 0; 
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;

      border-radius: 20px;
      overflow: hidden;
    }

    &__author {
      position: relative;
      z-index: 2;
    }

    &__author-name {
      color: $white;
    }
  }
}
