@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.section-news {
  &__outer-indent {
    margin-bottom: 64px;

    @include media(from_sm) {
      margin-bottom: 105px;
    }

    @include media(from_m) {
      margin-bottom: 160px;
    }

    @include media(from_l) {
      margin-bottom: 204px;
    }
  }

  & .section__title {
    margin-bottom: 24px;

    @include media(from_l) {
      margin-bottom: 64px;
    }
  }


  &__items {
    display: flex;
    flex-direction: column;
    margin: -24px 0 0;

    @include media(from_sm) {
      margin: -40px 0 0;
    }

    @include media(from_m) {
      flex-direction: row;
      flex-wrap: wrap;
      margin: -56px 0 0 -16px;
    }
  }

  &__item {
    width: 100%;
    margin: 24px 0 0;

    @include media(from_sm) {
      margin: 40px 0 0;
    }

    @include media(from_m) {
      width: calc(25% - 16px);
      margin: 56px 0 0 16px;
    }
  }

  &__item_wide {
    @include media(from_m) {
      width: calc(50% - 16px);
    }
  }

  &__item_order_first {
    order: -1;

    @include media(from_m) {
      order: unset;
    }
  }

  &__button-more {
    margin: 24px auto 0;

    @include media(from_sm) {
      margin: 56px auto 0;
    }

    @include media(from_m) {
      margin: 32px auto 0;
    }

    @include media(from_l) {
      margin: 48px auto 0;
    }
  }
}
