@import '@styles/vars.scss';
@import '@styles-mixins/media.scss';
@import '@styles-mixins/hoverEffects.scss';

.section-reviews {
  &__outer-indent {
    margin-bottom: 120px;

    @include media(from_sm) {
      margin-bottom: 200px;
    }

    @include media(from_m) {
      margin-bottom: 75px;
    }

    @include media(from_l) {
      margin-bottom: 100px;
    }
  }

  &__header {
    margin: 0 0 40px;

    @include media(from_sm) {
      margin: 0 0 56px;
    }

    @include media(from_m) {
      display: flex;
      margin: 0 0 48px;
    }

    @include media(from_l) {
      margin: 0 0 56px;
    }
  }

  & .section__title {
    margin: 0 0 24px;
    font-size: 24px;

    @include media(from_sm) {
      margin: 0 0 40px;
      font-size: 40px;
    }

    @include media(from_m) {
      margin: 0 24px 0 0;
    }

    @include media(from_l) {
      margin: 0 32px 0 0;
      font-size: 50px;
    }
  }

  &__info {
    display: flex;
    align-items: center;
  }

  &__info-icon {
    flex-shrink: 0;
    width: 40px;
    height: 40px;
    margin-right: 16px;

    background-repeat: no-repeat;
    background-size: 36px auto;
    background-position: center;
    background-image: url("data:image/svg+xml,%3Csvg width='34' height='34' viewBox='0 0 34 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 34C7.6109 34 0 26.3891 0 17C0 7.6109 7.6109 0 17 0C26.3891 0 34 7.6109 34 17C34 26.3891 26.3891 34 17 34ZM17 30.6C20.6069 30.6 24.0662 29.1671 26.6167 26.6167C29.1671 24.0662 30.6 20.6069 30.6 17C30.6 13.3931 29.1671 9.93384 26.6167 7.38335C24.0662 4.83285 20.6069 3.4 17 3.4C13.3931 3.4 9.93384 4.83285 7.38335 7.38335C4.83285 9.93384 3.4 13.3931 3.4 17C3.4 20.6069 4.83285 24.0662 7.38335 26.6167C9.93384 29.1671 13.3931 30.6 17 30.6V30.6ZM15.3 8.5H18.7V11.9H15.3V8.5ZM15.3 15.3H18.7V25.5H15.3V15.3Z' fill='%23D6D6D6'/%3E%3C/svg%3E%0A");

    @include media(from_sm) {
      width: 56px;
      height: 56px;
      margin-right: 16px;
      background-size: 56px auto;
    }

    @include media(from_m) {
      width: 32px;
      height: 32px;
      margin-right: 8px;
      background-size: 30px auto;
    }

    @include media(from_l) {
      width: 40px;
      height: 40px;
      margin-right: 16px;
      background-size: 40px auto;
    }
  }

  &__info-text {
    max-width: 224px;

    font-weight: 600;
    font-size: 14px;
    line-height: 120%;
    color: $color_text_secondary;

    @include media(from_sm) {
      max-width: 400px;
      font-size: 24px;
    }

    @include media(from_m) {
      max-width: 176px;
      font-size: 10px;
    }

    @include media(from_l) {
      max-width: 224px;
      font-size: 14px;
    }
  }

  &__info-link {
    text-decoration: none;
    font-weight: 700;
    color: $blue-light;

    @include defaultHover();
  }
}
